
@media all {
  .animate-component-container {
    position: relative;
    z-index: 1;
  }
}
/* fade */
@media all {
  .fade-mount-start {
    opacity: 0;
  }
  .fade-mount-end {
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    opacity: 1;
  }
  .fade-unmount-start {
    opacity: 1;
  }
  .fade-unmount-end {
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    opacity: 0;
  }
}



