
@media all {
	.trello {
		background-color: #0079bf;
	}
	.page-container {
		background-color: #fff;
	}
	.main-container {
		height: 100vh;
		padding: 60px 20px 0;
	}
	.columns-list {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: start;
		    align-items: flex-start;
		margin: -10px;
		width: 100%;
		height: 100%;
		overflow: auto;
	}
	.add-column-link {
		padding: 7px 10px;
		color: rgba(255, 255, 255, 0.7);
		background-color: rgba(0,0,0,.15);
		text-decoration: none;
		margin: 10px;
		-webkit-transition: .3s ease;
		-o-transition: .3s ease;
		transition: .3s ease;
		-ms-flex-negative: 0;
		    flex-shrink: 0;
	}
	.add-column-link:hover {
		color: rgba(255, 255, 255, 0.7);
		background: rgba(0,0,0,.3);
		text-decoration: none;
	}

	::-webkit-scrollbar{width:10px;height:10px}
	::-webkit-scrollbar-thumb{background-color:rgba(50,50,50,.25);border:2px solid transparent;border-radius:10px;background-clip:padding-box}
	::-webkit-scrollbar-track{background-color:rgba(50,50,50,.05)}
	::-webkit-scrollbar-thumb:hover{background-color:rgba(50,50,50,.5)}
}

/* animation */
@media all {
  .example-appear {
    opacity: 0.01;
  }
  .example-appear.example-appear-active {
    opacity: 1;
    -webkit-transition: opacity .5s ease-in;
    -o-transition: opacity .5s ease-in;
    transition: opacity .5s ease-in;
  }
}



