@media all {
	.header-user {
		position: absolute;
		right: 0;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	.header-user__avatar-container {

	}
	.header-user__avatar {
		width: 35px;
		height: 35px;
		background-position: center;
		background-size: cover;
		border-radius: 50%;
	}
	.header-user__exit {
		color: #fff;
		margin-left: 10px;
	}
	.header-user__exit:hover {
		color: #fff;
		text-decoration: underline;
	}
}


